<template>

    <div :id="'chat' + groupId" class="w-100">
        <!-- <div>
                                    <button @click="startRecording" :disabled="isRecording">Start Recording</button>
                                    <button @click="stopRecording" :disabled="!isRecording">Stop Recording</button>
                                    <button @click="sendAudio">Send</button>
                                    <audio v-if="audioUrl" :src="audioUrl" controls></audio>
                                </div>{{ audioUrl }} -->
        <div v-if="groupShow || groupShowProps" class="div_forDifferentTabs content chatdv chat-region">
            <div class="dev_contact-internal-tabs" id="pills-usergroups" v-if="topFilterSelect == 'Groups'">
                <div class="closeDeiv btn btn-secondary save float-right mb-2" @click="backToChat()">Back To Chat</div>
                <div class="clearfix"></div>
                <div id="accordion-window" class="chatpanesbasemid">
                    <div class="recent-contacts mt-0">
                        <div class="recent-contacts-listing-wrapper min-height65">
                            <div id="accordion" class="grouplistingbase">

                                <div class="card" v-for="group in store.getGroupList">
                                    <div class="card-header" id="heading42559">
                                        <h5 class="mb-0">
                                            <button class="btn btn-link collapsed" is-admin="true"
                                                data-toggle="collapse" data-target="#collapse42559"
                                                aria-expanded="false" aria-controls="collapse42559"
                                                @click="activeRecord(group.grouP_ID)">
                                                {{ group.grouP_NAME }}
                                                <i class="fa fa-accordian float-left" aria-hidden="true"></i>
                                            </button>
                                            <span class="adminbase text-success font-weight-bold">{{ group.fulL_NAME
                                                }}</span>
                                            <div class="uc_groupdel text-right">
                                                <a id="groupAddMember" @click="editGroup(group)" class="d-inline-block"
                                                    title="Edit"><i class="fa fa-pencil txt-orange mr-2"></i></a>
                                                <a id="groupDelete" @click="deleteGroup(group.grouP_ID)"
                                                    class="mr-3 d-inline-block" title="Delete"><i
                                                        class="fa fa-trash text-danger"></i></a>
                                            </div>
                                        </h5>
                                    </div>
                                    <div id="collapse42559" aria-labelledby="heading42559" data-parent="#accordion"
                                        style="margin-top: 14px;" v-if="activeGroup == group.grouP_ID">
                                        <div class="card-body call_list">

                                            <div class="member card-contact recent-contacts-listing" data-userid="65239"
                                                v-for="member in store.getUserByGroupId">
                                                <div class="member-img">
                                                    <img
                                                        src="https://talygenv.github.io/Vue-UI-Content/images/UC/uc_user_new.jpg">
                                                </div>
                                                <div class="member-detail">
                                                    <p><b>{{ member.useR_NAME }} </b></p>
                                                    <div class="log uc_fav pr-2 icons"
                                                        @click="deleteGroupUser(member.useR_ID, group.grouP_ID)">
                                                        <a class="singleAccUserDel phone" href="javascript:;"
                                                            title="Remove"><i class="fa fa-trash"></i></a>
                                                    </div>
                                                </div>
                                            </div>
                                            <div v-if="store.getUserByGroupId.length == 0">
                                                {{ $t('NoRecordfound') }}
                                            </div>
                                        </div>
                                    </div>
                                </div>

                                <div> </div>
                            </div>
                        </div>
                    </div>
                </div>
                <div id="accordion-window-error" class="text-center text-danger border p-2 no-record"
                    v-if="store.getGroupList.length == 0 || store.getGroupList == null">
                    No record(s) found
                </div>
            </div>
            <div class="chat-panel chat-content" id="creategroup" v-if="isCreateGroup && topFilterSelect != 'Groups'">

                <Form id="AddGroup" ref="newGroup" v-slot="{ valid, errors }">
                    <div class="chat-header">
                        <div class="name">
                            {{ $t('CreateChatGroup') }}
                        </div>
                    </div>
                    <div class="form create-group-form">

                        <div class="form-group">
                            <label>{{ $t('GroupChatName') }}</label>
                            <input id="groupTitle" class="form-control" maxlength="30" v-model="Grouptitle">
                            <small class="text-danger" v-if="Grouptitle.length == 30">{{ $t('CharacterLimit') }}</small>
                        </div>
                        <div class="form-group">
                            <label>{{ $t('Members') }}:</label>
                            <multiselect :close-on-select="false" limit="2" v-model="selectedMembers"
                                :options="MemberList" track-by="USER_ID" label="CONTACTNAME"
                                placeholder="Select memberss" multiple></multiselect>
                        </div>
                        <div class="bottom-btn">
                            <a href="javascript:;" id="createGroupBtn" class="btn btn-success" title="Create"
                                v-on:click="updategroup ? updateGroup() : CreateGroup()">{{ updategroup ?
        $t('Update') : $t('Create') }}</a>
                            <a href="javascript:;" id="createGroupCancelBtn" class="btn btn-danger"
                                v-on:click="updategroup ? canceleGroup() : backToChat();">{{
        $t('Cancel') }}</a>
                        </div>
                    </div>
                </Form>
            </div>
        </div>

        <div v-else class="div_forDifferentTabs content chatdv chat-region">
            <div class="chat-header user-info">
                <div class="name text-truncate">
                    <img class="profile-img " v-if="Groupdata.IS_GROUP == false"
                        :src="Groupdata.AVATAR != '' ? Groupdata.AVATAR : defaultImg" alt="profile">
                    <i class="fa fa-users" v-if="Groupdata.IS_GROUP"></i>
                    {{ groupNameDisplay }}
                </div>
                <div class="chat-links">
                    <a href="javascript:;" title="" tooltip="tooltip" v-on:click="VoiceCallClick"
