                        v-if="Groupdata.IS_GROUP == true ? false : chatUserInfo.IS_GROUP == true ? false : true"><i
                            class="fa fa-phone"></i></a>
                    <a href="javascript:;" title="" tooltip="tooltip" data-original-title="Video Call"
                        class="video_icon a_VideoCall mr-1" @click="videoCreate(chatUserInfo)"><i
                            class="fa fa-video-camera"></i></a>
                    <a href="javascript:;" v-on:click="Getgroupusers(chatUserInfo)"
                        v-if="Groupdata.IS_GROUP || chatUserInfo.IS_GROUP" id="pills-group-tab" title=""
                        tooltip="tooltip" data-original-title="Users" class="users user_click mr-1"><i
                            class="fa fa-user"></i></a>
                    <a href="javascript:;" v-on:click="ChatLinkClick()" v-if="isChatActive" id="pills-profile-tab"
                        title="" tooltip="tooltip" data-original-title="Chat  Files" class="file mr-1"><i
                            class="fa fa-file-text-o "></i></a>
                    <a href="javascript:;" id="pills-home-tab" v-on:click="FileLinkClick()" v-if="isFileActive" title=""
                        tooltip="tooltip" data-original-title="Chat" class="icon_chatting file active"><i
                            class="fa fa-comments "></i></a>
                    <a id="pills-usergroups-tab" title="" class="users nav-link" data-toggle-tooltip="tooltip"
                        data-original-title="Groups"><i class="fa fa-users"
                            @click="getContactRecordBy('Groups')"></i></a>

                </div>
            </div>
            <div class="ball-loader" v-if="isDataLoaded">
                <div class="ball-loader-ball ball1">
                </div>
                <div class="ball-loader-ball ball2">
                </div>
                <div class="ball-loader-ball ball3">
                </div>
            </div>
            <!-- <div class="dev_msgbx_left_for_attachment" v-if="chatHistoryDetails.resultSet">
                <div class="files-tab" v-if="chatHistoryDetails.resultSet.length==0" data-simplebar="" style="height:80vh; overflow-y:auto"><div class="text-center text-danger border p-2">No record(s) found</div></div>
            </div> -->

            <div class="msgbx_left position-relative" v-if="isChatActive">
                <!-- div for message chat history details -->
                <!--  v-chat-scroll="{ enabled: enabled }" -->
                <div class="scroll-wrapper scrollbar-inner scroll-content">
                    <div class="scrollbar-inner scroll-content" v-chat-scroll="{ enabled: enabled }"
                        id="div_chatDetails"
                        style="height: auto;margin-bottom: 0px;    margin-right: 0px;    max-height: calc(100vh - 250px) !important;">
                        <div id="chat-window" class="chatpanesbasemid">
                            <div id="message-list"
                                :class="{ 'messageslists_uc60 me_default': selectedLayout == 'Default', 'messageslists_uc60 me_right': selectedLayout == 'Right', 'messageslists_uc60 me_left': selectedLayout == 'Left', 'groupchat': Groupdata.IS_GROUP }">
                                <!-- <div v-if="isDataLoaded == false && chatHistoryDetails.resultSet.length < chatHistoryDetails.resultSet[0].TOTAL_RECORDS && Loaddatashow"
                                    id="div_loadMoreChatMessages" class="text-center loadMoreButtonforChat">
                                    <a href="javascript:;" class="btn btn-info" v-on:click="Loadmore">Load more...</a>
                                </div> -->
                                <template v-for="filterByDate in filterSameUser(chatHistoryDetails.resultSet)">
                                    <div class="dividerdate mb-4 mt-4">
                                        <div class="dividerdate_box">{{ getPreviousDay(filterByDate.date) }}</div>
                                    </div>
                                    <div class="clearfix"></div>
                                    <template v-for="(item, index) in filterByDate.data">
                                        <div ref="chat" :class="{
        'own-message message-bx': true, 'blinking': (blinkText == item.CHATLOG_ID || blinkRepliedText == item.CHATLOG_ID), 'parentthread': item.sameName == 0,
        'myself': item.FROM_USER_ID == store.getUserInformation.ID,
        'yourself': item.FROM_USER_ID != store.getUserInformation.ID
    }"
                                            :id="'chatLog_' + item.CHATLOG_ID">
                                            <div class="user-avatar"
                                                v-if="item.FROM_USER_ID != store.getUserInformation.ID">
                                                <img v-if="item.sameName == 1"
                                                    :src="item.AVATAR == blobUserUrl || item.AVATAR == '' || item.AVATAR == null ? defaultImg : item.AVATAR"
