                                                    v-on:click="ShowTransitionOnReply(item.PARENT_DETAIL)">
                                                    <div class="row no-margin message-info-row replyBack ml-0">
                                                        <div class="arrow"></div>
                                                        <p data-content="username" class="message-sendername w-100">
                                                            {{ item.PARENT_DETAIL == undefined ? item.PARENT_DETAIL :
        item.PARENT_DETAIL.split('~')[1] }}</p>
                                                        <div class="messagebody">
                                                            <p data-content="body" class="message-body"
                                                                v-html="item.PARENT_MESSAGE"></p>
                                                        </div>
                                                    </div>
                                                    <div style="clear:both"></div>
                                                </a>

                                                <div class="message-body"
                                                    v-if="item.MESSAGE != 'attachment' && item.ATTACHMENT != 1">
                                                    <p v-html="item.MESSAGE"></p>

                                                </div>
                                                <div class="message-body"
                                                    v-else-if="item.MESSAGE == 'voice' && item.ATTACHMENT == 1">
                                                    <!-- <p  v-html="item.MESSAGE"></p> -->
                                                    <audio v-if="item.FILEPATH" :src="item.FILEPATH" controls></audio>
                                                </div>

                                                <div class="message-body mb-2 onlyattachment"
                                                    v-else-if="item.MESSAGE == 'attachment' && item.ATTACHMENT == 1">
                                                    
                                                    <!-- <p>{{ item.STORAGE_FILE_SIZE }}</p> -->
                                                    <a :class="{ 'attachment box p-2 border d-inline-block mb-2': item.FILEEXTENSION == '.jpg' || item.FILEEXTENSION == '.png', 'attachment box mb-2 verticlealignmiddle text-center': item.FILEEXTENSION == '.pdf' }"
                                                        @click="openLightbox(item.FILEPATH, item.FILEEXTENSION, item.STORAGE_FILE_NAME)"
