                                                        :title="item.STORAGE_FILE_NAME">
                                                        <img v-if="item.FILEEXTENSION == '.jpg' || item.FILEEXTENSION == '.png'"
                                                            @click="openLightbox(item.FILEPATH, item.FILEEXTENSION, item.STORAGE_FILE_NAME)"
