                                                        <i class="m-0 p-0 fa fa-file-pdf-o"
                                                            v-if="item.FILEEXTENSION == '.pdf'"
                                                            style="margin-right:0px;line-height:2em;"></i>
                                                            
                                                    </a>
                                                    <!-- style="width:100%;height:70px;" -->
                                                    <!-- <a class="attachment box"   @click="openLightbox(item.FILEPATH,item.FILEEXTENSION,item.STORAGE_FILE_NAME)">
                                                <i class="fa fa-file-image-o"></i> {{ item.STORAGE_FILE_NAME }}
                                            </a> -->
                                                </div>
                                                
                                                <div class="message-header">
                                                    <span class="author-name mr-2"
                                                        v-if="item.FROM_USER_ID != store.getUserInformation.ID && item.IS_GROUP == true">{{
        item.USER_NAME }}</span>
                                                    <span class="author-name message-time"
                                                        v-if="DateDifference(item.CREATED_AT) == 'Now'">{{
        DateDifference(item.CREATED_AT) }}</span>
                                                    <span class="author-name message-time" v-else>{{
        getTime(item.CREATED_AT) }}</span>
                                                </div>

                                                <div class="msg-actions">
                                                    <ul>
                                                        <li><a id="msgReplyOwn" class="noToolTip"
                                                                v-on:click="reactEmoji" href="javascript:;"
                                                                title="Reply"><i class="fa-solid fa-face-smile"
                                                                    aria-hidden="true"></i></a>
                                                        </li>
                                                        <li><a id="msgReplyOwn" class="noToolTip"
                                                                v-on:click="ReplyMessageToUser(item)"
                                                                href="javascript:;" title="Reply"><i class="fa fa-reply"
                                                                    aria-hidden="true"></i></a>
                                                        </li>
                                                        <li><a id="msgForwardOwn" class="noToolTip"
                                                                v-on:click="ForwardMessageTo(item)" href="javascript:;"
                                                                title="Forward Message"><i class="fa fa-share"
                                                                    aria-hidden="true"></i></a></li>
                                                        <li><a id="msgReplyDelete" class="noToolTip" v-if="item.FROM_USER_ID==store.getUserInformation.ID && formatDateToISO(item.CREATED_AT).split('T')[0] == todayDate &&  item.MESSAGE != 'attachment' && item.ATTACHMENT != 1 && item.MESSAGE != 'voice' && item.ATTACHMENT != 1 && checkIsGIFForEditMessage(item.MESSAGE)"
                                                                v-on:click="EditMessage(item, index)"
                                                                href="javascript:;" title="Edit Message"><i
                                                                    class="fa fa-pencil" ></i></a></li>
                                                        <li><a id="msgReplyDelete" class="noToolTip"
                                                                v-on:click="editMessageInfo(item, index)" v-if="item.FROM_USER_ID==store.getUserInformation.ID && item.MESSAGE != 'attachment' && item.ATTACHMENT != 1 && item.MESSAGE != 'voice' && item.ATTACHMENT != 1 && checkIsGIFForEditMessage(item.MESSAGE)"
                                                                href="javascript:;" title="Message info"><i class="fa fa-info-circle" aria-hidden="true"></i></a></li>
                                                        <li><a id="msgReplyDelete" class="noToolTip"
                                                                v-on:click="DeleteMessage(item, index)"
                                                                href="javascript:;" title="Delete Message"><i
                                                                    class="fa fa-trash" aria-hidden="true"></i></a></li>
                                                    </ul>
                                                    <VueEasyLightbox :visible="visible" :imgs="imgs" :index="index" @hide="handleHide" >
           
                                                        <template v-slot:toolbar="{ toolbarMethods }">
                                                    <button @click="downloadImage(item.STORAGE_FILE_ID,item.STORAGE_FILE_NAME)">{{ $t('DownloadFile') }}</button>
                                                    <button @click="toolbarMethods.zoomIn">Zoom In</button>
                                                    <button @click="toolbarMethods.zoomOut">Zoom Out</button>
                                                    <button @click="toolbarMethods.rotateLeft">Rotate Left</button>
                                                    <button @click="toolbarMethods.rotateRight">Rotate Right</button>
                                                    </template>
                                                
                                                </VueEasyLightbox>
                                                </div>
                                            </div>

                                        </div>
                                        <div class="clearfix"></div>
                                    </template>
                                </template>
                                <div class="own-message message-bx myself progressbasediv" id="fileProgressId"
                                    v-if="fileInProgressShow" ref="progressDiv" tabindex="0">
                                    <!-- <div class="control-section" style="
                                    float: left;
                                    width: 100%;
                                    text-align: center;
                                "><div><em class="fa fa-upload"></em> Sending file in process...</div>
                                <progress class="bg-success" id="progressBar" max="100" :value="progressValue" style="width: 100%;"></progress></div> -->
                                    <div class="control-section" style=" float: left; width: 100%; text-align: center;">
                                        <em class="fa fa-upload"></em> Sending file in process...
                                    </div>
                                    <div class="progress w-100">
                                        <div class="progress-bar bg-success progress-bar-striped" role="progressbar"
                                            :style="'width: ' + progressValueShow + '%'" :aria-valuenow="progressValue"
                                            aria-valuemin="0" aria-valuemax="100"></div>
                                    </div>

                                </div>



                            </div>
                        </div>
                    </div>
                </div>



                <div v-if="isShowTyping" class="useristypingbase" :class="{ 'group': isChatOnGroup == true }">
                    <div class="useristyping2" v-if="isChatOnGroup == false && userGroupId == isTypingGroupId">{{ isTyping
                        }} <img :src="isTypingImage" /></div>
                    <!-- <div class="useristyping2" v-if="isChatOnGroup==true" ><img v-for="itmImg in isTyping" :src="itmImg" /> <img :src="isTypingImage" /></div> -->

                    <div class="groupistyping" v-if="isChatOnGroup == true && userGroupId == isTypingGroupId">
                        <template>
                            <div class="group_dp float-left" v-for="itmImg in isTyping">
                                <img :src="itmImg == null ? defaultImg : itmImg" />
                            </div>
                            <div class="useristyping2">
                                <img :src="isTypingImage" />
                            </div>
                        </template>
                    </div>
                </div>


                <div class="message-footer" v-if="userGroupId">
                    <div class="chattingarea_outer">
                        <form id="message-form">
                            <div id="replyhtml" class="replyhtmlbase_hs" v-if="isReplytoMessage">
                                <div id="replyTxt" class="replywidth" v-for="rplyonitm in replyOnData">
                                    <div class="user-avatar">
                                        <img :src="rplyonitm.AVATAR != '' ? rplyonitm.AVATAR : defaultImg"
                                            alt="profile">
                                    </div>
                                    <div class="message-data replycopied" :id="rplyonitm.CHATLOG_ID">
                                        <div class="message-header">
                                            <span class="author-name mr-2">{{ rplyonitm.USER_NAME }}</span>
                                            <span class="author-name message-time">{{
        DateDifference(rplyonitm.CREATED_AT)
    }}</span>
                                        </div>
                                        <a :name="rplyonitm.MESSAGE_TOKEN" href="javascript:;"
                                            class="width_100 hyperLink">
                                            <div style="clear:both"></div>
                                        </a>
                                        <div class="message-body">
                                            <p v-html="truncateMessage(rplyonitm.MESSAGE)"></p>
                                        </div>
                                    </div>
                                </div>
                                <div class="replycross"><a id="replycross" href="javascript:;"
                                        v-on:click="ReplyCross"><i class="fa fa-times text-danger"></i></a></div>
                            </div>
                            <div id="gifDiv" class="replyhtmlbaseGIF" v-if="isShowGifPanel">

                                <div class="ball-loader" v-if="isDataGifLoaded">
                                    <div class="ball-loader-ball ball1">
                                    </div>
                                    <div class="ball-loader-ball ball2">
                                    </div>
                                    <div class="ball-loader-ball ball3">
                                    </div>
                                </div>
                                <div class="imgGif" v-for="gifItems in GifImageData">
                                    <img style="float:left;margin-right:5px;" data-isgif="1" class="gifdata" id="gif0"
                                        v-on:click="SetGifToSendArea(gifItems)" :src="gifItems.media[0].gif.url" alt="">
                                    <!-- gifItems.media[0].webp_transparent.preview -->
                                    <a class="gif_cross" v-on:click="RemoveGifToSend()"><i
                                            class="fa fa-close text-danger">
                                        </i>
                                    </a>
                                </div>

                            </div>
                            <!-- <div class="search-menu">
                                <input type="text" id="txtChatMessageSearch" placeholder="Search here" @keydown.enter.exact.prevent="SearchForGIF()"
                                        v-model="txtSearchMessage" v-if="isSearchTxt == false" autocomplete="off"
                                        data-emojiable="true" rows="1">
                                <button type="button" id="chatSearchFind" v-if="txtSearchMessage == ''"
                                    title="Search with User/Group"><i class="fa fa-search"></i></button>
                                <button type="reset" id="search-Forward-Cross" v-if="txtSearchMessage != ''"
                                    v-on:click="clearTextFromChatGroups" class="close-icon">X</button>
                            </div> -->
                            <div id="div_chatTextbox" class="chattingarea">
                                <div class="msgarea" style="height: 52px !important;">
                                    <textarea id="txtChatMessage" placeholder="Your Message" v-model="txtMessage"
                                        v-if="isSearchTxt == true && isMessageEdit==false" :style="{ display: cssArea, }" ref="focusText"
                                        @keydown.enter.exact.prevent="sendMessage(chatHistoryDetails)"
                                        autofocus="autofocus" v-on:keyup="checkIsTyping($event)" autocomplete="off"
                                        data-emojiable="true" rows="1">
                                    </textarea>
                                    <textarea id="txtChatMessageSearch" placeholder="Search here"
                                        @keydown.enter.exact.prevent="SearchForGIF()" v-model="txtSearchMessage"
                                        v-if="isSearchTxt == false && isMessageEdit==false" autocomplete="off" data-emojiable="true" rows="1">
                                    </textarea>
                                    <textarea id="txtChatEdtMessage" placeholder="Edit Message" v-model="txtMessage"
                                        v-if="isMessageEdit == true" :style="{ display: cssArea, }" ref="focusText"
                                        @keydown.enter.exact.prevent="sendMessage(chatHistoryDetails)"
                                        autofocus="autofocus" v-on:keyup="checkIsTyping($event)" autocomplete="off"
                                        data-emojiable="true" rows="1">
                                    </textarea>
                                    
                                    <div class="emojionearea  emojionearea-inline">
                                        <div class="emojionearea-editor emoji-wysiwyg-editor"
                                            @keydown.enter.exact.prevent="sendMessage(chatHistoryDetails)" id="emjArea"
                                            contenteditable="true" v-if="gifSource != ''" placeholder="Your message"
                                            tabindex="0" dir="ltr" spellcheck="false" autocomplete="off"
                                            autocorrect="off" autocapitalize="off">
                                            <!-- <img data-isgif="1" id="gif0" :src="gifSource" alt=""
                                                style="float:left;margin-right:5px;"> -->
                                            <!-- <a class="gif_cross" v-on:click="RemoveGifToSend()">
                                                <i class="fa fa-close text-danger" style="cursor:pointer;">
                                                </i>
                                            </a> -->
                                        </div>
                                    </div>
                                </div>

                                <div v-if="txtMessage == ''" class="uc_msgsicons">
                                    <a title="Voice" id="Voice" href="javascript:;" v-if="isRecording == false" @click="startRecording"
                                        class="icon_gif noToolTip"><i class="fa fa-microphone" aria-hidden="true"></i>
                                    </a>
                                    <a title="Voice" id="Voice" href="javascript:;" v-if="isRecording"
                                        @click="stopRecording" class="icon_gif noToolTip"><i class="fa fa-stop"
                                            aria-hidden="true"></i>
                                    </a>


                                    <a title="Gif" id="gif" href="javascript:;" class="icon_gif noToolTip" v-if="isGif"
                                        v-on:click="ShowGifDiv"><i class="fa fa-file-image-o">
                                        </i>
                                    </a>
                                    <a title="File Sharing" id="attachmentFile" href="javascript:;"
                                        v-on:click="AttachmentFileClick" v-if="isGif" class="icon_clip noToolTip">
                                        <i class="fa fa-paperclip"></i></a>
                                    <a title="Chat Search" id="a_chatSearch" v-if="isShearchForMsg"
                                        v-on:click="SearchFortxt()" href="javascript:;" class="icon_search noToolTip"><i
                                            class="fa fa-search"></i></a>

                                    <a title="GIF Search" id="a_GIFSearch" v-if="searchGIFbyTxt"
                                        v-on:click="SearchForGIF()" href="javascript:;" class="icon_search noToolTip"><i
                                            class="fa fa-search"></i></a>

