        },
        progressValue: {
            type: Number
        }
    },
    components: {
        "emoji-picker": EmojiPicker,
        VueEasyLightbox,
        VEmojiPicker
    },
    data() {
        return {
            name: 'Demo',
            txtMessage: '',
            visiblePop: false,
            moment: moment,
            sender: {},
            type: 'web',
            mobileType: '',
            isReplytoMessage: false,
