            input: '',
            search: '',
            isShowCloseSearchIcon: false,
            txtSearchForward: '',
            ForwardUsersList: [],
            selectUserIds: [],
            chaTIdForForward: '',
            isChatActive: true,
            isFileActive: false,
            GifImageData: [],
            isShowGifPanel: false,
            gifSource: '',
            tmpFilter: [],
            isDataLoaded: true,
            isSearchTxt: true,
            txtSearchMessage: '',
            cssArea: '',
            isGif: true,
            GroupUsersData: [],
            isShowGroupUsers: false,
            GroupData: [],
            sortExp: '',
            sortBy: '',
            city: '',
            UsersListDataForGroups: [],
            blinkText: null,
            indexArr: [],
            scrollCount: 0,
            blobUrl: '',
            containerName: '',
            imgPath: '',
            blobUserUrl: '',
            pageNum: 1,
            pageSize: '',
            departmentId: '',
            Loaddatashow: true,
            lastRow: 0,
            isShearchForMsg: true,
            searchGIFbyTxt: false,
            ChatFileList: [],
            blinkRepliedText: null,
            isTyping: '',
            typing: false,
            timeout: undefined,
            user: null,
            isShowTyping: false,
            isTypingImage: 'https://talygenv.github.io/Vue-UI-Content/images/UC/typingo.gif',
            allchatPersons: [],
            groupShow: false,
            groupId: '',
            Grouptitle: '',
            selectedMembers: [],
            activeGroup: '',
            updategroup: false,
            MemberList: [],
            enabled: true,
            isChatOnGroup: false,
            isTypingGroupId: '',
            isDataGifLoaded: false,
            isGrouped: false,
            selectedLayout: 'Default',
            lastRecordNumber: 50,
            imgs: '',  // Img Url , string or Array of string
            visible: false,
            index: 0,  // default: 0
            IsreactOnMessage: false,
            isRecording: false,
            mediaRecorder: null,
            audioChunks: [],
            audioUrl: null,
            uploadUrl: process.env.VUE_APP_DOCSTORAGE_API + '/UploadStreamNew',
            headphonesConnected: false,
            isMessageEdit:false,
            MessagePlaceholder:'',
            chatMessageToken:'',
            showMessageInfo:false,
            EditMessagInfoListData:[]
            
        };
    },
    computed: {
        groupShowProps: function () {
            return this.isCreateGroup
        },
        fileInProgressShow: function () {
            return this.fileInProgress
        },
        progressValueShow: function () {
            return this.progressValue
        },
        todayDate: function()
        {
            return new Date().toISOString().split('T')[0]; // Format the date as YYYY-MM-DD
        }
    },

    watch: {
        'store.$state.globalSearch': {
            immediate: true,
            handler() {

                // update locally relevant data

                if (this.store.$state.globalSearch != '') {
                    this.enabled = false
                    this.HighlightOnReplyItem(this.store.$state.globalSearch)
                } else {
                    this.enabled = true
                }

            }
        }
    },
    mounted() {

        const listElm = document.querySelector('#div_chatDetails');
        if (listElm != null) {
            listElm.addEventListener('scroll', e => {

                if (listElm.scrollTop == 1) {
                    this.Loaddatashow = true;
                    //this.Loadmore()
                }
            });
        }
        //this.$refs.focusText.focus();

        window.addEventListener("resize", this.updateDimensions);
        let myObj = {
            companyId: this.store2.GetUserInfo.CompanyId,
            userId: this.store2.GetUserInfo.ID,
            emaiId: this.store2.GetUserInfo.Email,
            name: this.store2.GetUserInfo.Name,
            imgPath: this.blobUserUrl + this.store2.GetUserInfo.Avatar,
            type: this.type
        }
        //this.$socket.emit('user total', myObj);

    },
    beforeDestroy() {
        window.removeEventListener("resize", this.updateDimensions);
    },
    created: async function () {
        let vm = this;
        vm.MessagePlaceholder='Your Message'
        vm.GetContactList()

        vm.GetUserInfo = vm.GetUserInfo ? vm.GetUserInfo : vm.store.getUserInformation;

        vm.containerName = vm.GetUserInfo?.ContainerName;
        vm.messageSetting()
