        // await vm.GetGifImages();
        await vm.BindForwardUsers();
        vm.tmpFilter = vm.ForwardUsersList;
        vm.type = 'web'
        if (vm.mobileType) {
            vm.type = "mobile";
        }
        let obj = {
            companyId: vm.store2.GetUserInfo.CompanyId,
            id: vm.store2.GetUserInfo.ID,
            emaiId: vm.store2.GetUserInfo.Email,
            name: vm.store2.GetUserInfo.Name,
            imgPath: vm.blobUserUrl + vm.store2.GetUserInfo.Avatar,
            type: vm.type,
            userId: this.store2.GetUserInfo.ID,
        }
        vm.selectedMembers = []
        vm.selectedMembers.push({
            "USER_ID": vm.GetUserInfo.ID,
            "CONTACTNAME": vm.GetUserInfo.Name
        })
        vm.sender = obj;
        //await vm.$socket.emit('user total', vm.sender);
        vm.isDataLoaded = false;
        //vm.$refs.focusText.focus();


        //  setTimeout(function () {
        //      var lastindx=vm.chatHistoryDetails.resultSet.length-1;
        //      var element = document.getElementById('chatLog_' + vm.chatHistoryDetails.resultSet[lastindx].CHATLOG_ID);
        //      element.scrollIntoView({ behavior: 'smooth', block: 'start' });
        //  }, 1200);

    },

    sockets: {
        connect() {
            console.log('Connected to server');
        },
        disconnect() {
            console.log('Disconnected from server');
        },
        newMessageAttachment: async function (message) {
            
            var vm = this;

            var currentdate = new Date()
            if (message.roomId == vm.userGroupId && message.userId == vm.sender.id) {
                vm.chatHistoryDetails.resultSet.push(
                    {
                        CHATLOG_ID: message.chatId,
                        MESSAGE: message.text,
                        GROUP_ID: message.roomId,
                        FROM_USER_ID: message.from,
                        CREATED_AT: currentdate,
                        MESSAGE_TOKEN: message.token,
                        AVATAR: message.fromUserImg,
                        IS_READ: 0,
                        USER_NAME: message.name,
                        FILEPATH: message.url,
                        STORAGE_FILE_NAME: message.file,
                        STORAGE_FILE_SIZE: message.size,
                        ATTACHMENT: 1,
                        FILEEXTENSION: '.jpg'
                    }
                )
            }
            await vm.$socket.emit('get All Chat Persons', vm.sender);
            var param = new Object(); // update receiver end with message read
            param = message;
            await vm.$socket.emit('user chat Read', param);
            await vm.$emit('readMessageUpdate', message.roomId, vm.sender);

        },
        newMessage: function (message) {

            var vm = this;
            var currentdate = new Date()
            if(message.chatMessageToken=='' || message.chatMessageToken==undefined)
            {
                    if (message.parentId) {
                    vm.chatHistoryDetails.resultSet.push(
                        {
                            CHATLOG_ID: message.chatId,
                            MESSAGE: message.text,
                            GROUP_ID: message.room,
                            FROM_USER_ID: vm.store2.GetUserInfo.ID,
                            CREATED_AT: currentdate,
                            MESSAGE_TOKEN: message.msg_token,
                            AVATAR: message.sImg,
                            IS_READ: 0,
                            USER_NAME: message.from,
                            PARENT_MESSAGE: message.parent_msg,
                            CHAT_LOG_REF_ID: message.parentId,
                            PARENT_DETAIL: message.msg_detail,
                        }
                    )
                }
                else {
                    vm.chatHistoryDetails.resultSet.push(
                        {
                            CHATLOG_ID: message.chatId,
                            MESSAGE: message.text,
                            GROUP_ID: message.room,
                            FROM_USER_ID: vm.store2.GetUserInfo.ID,
                            CREATED_AT: currentdate,
                            MESSAGE_TOKEN: message.msg_token,
                            AVATAR: message.sImg,
                            IS_READ: 0,
                            USER_NAME: message.from,
                        }
                    )
                }
            }
            else
            {
                 var findChatMessageIndex= vm.chatHistoryDetails.resultSet.findIndex(x=>x.MESSAGE_TOKEN==message.chatMessageToken)
               
                if (findChatMessageIndex !== -1) {
                // Create a new object with the updated values
                // const updatedMessage = {
                //     CHATLOG_ID: message.chatId,
                //     MESSAGE: message.text,
                //     GROUP_ID: message.room,
                //     FROM_USER_ID: vm.store2.GetUserInfo.ID,
                //     CREATED_AT: message.createdAt,
                //     MESSAGE_TOKEN: message.msg_token,
                //     AVATAR: message.sImg,
                //     IS_READ: 0,
                //     USER_NAME: message.from,
                //     PARENT_MESSAGE: message.parent_msg,
                //     CHAT_LOG_REF_ID: message.parentId,
                //     PARENT_DETAIL: message.msg_detail,
                // };

                // Use Vue.set to ensure reactivity
                vm.$set(vm.chatHistoryDetails.resultSet[findChatMessageIndex], 'MESSAGE', message.text);
                // vm.$set(vm.chatHistoryDetails.resultSet, findChatMessageIndex, updatedMessage);
            }
            }
            

            vm.isDataLoaded = false;
        },
        broadcastContact: async function (data) {
            
            var vm = this;
            var currentdate = new Date()
            if(data.chatMessageToken=='' || data.chatMessageToken==undefined)
                {
                if (data.room > 0) {
                    if (data.userId == vm.sender.id && data.room == vm.userGroupId) {
                        vm.chatHistoryDetails.resultSet.push(
                            {
                                CHATLOG_ID: data.chatId,
                                MESSAGE: data.msg,
                                GROUP_ID: data.room,
                                FROM_USER_ID: data.fromUserId,
                                CREATED_AT: currentdate,
                                MESSAGE_TOKEN: data.msg_token,
                                AVATAR: data.fromUserImg == vm.blobUserUrl ? vm.defaultImg : data.fromUserImg,
                                IS_READ: 0,
                                FILEPATH: data.url,
                                IS_FORWARD: data.is_forward,
                                REF_FORWARD_ID: data.REF_FORWARD_ID,
                                USER_NAME: data.fromUser,
                                PARENT_MESSAGE: data.parentTxt == '' ? "" : data.parentTxt,
                                CHAT_LOG_REF_ID: data.parentId,
                                PARENT_DETAIL: data.msg_detail,
                                STORAGE_FILE_NAME: data.file,
                                STORAGE_FILE_SIZE: data.size,
                                ATTACHMENT:data.is_attachment,
                                FILEEXTENSION:data.FILEEXTENSION


                            }
                        )
                    }
                }
            }   
            else
