        notifyMeChat: function (data) {
            $.notifyClose();
            var body = "You have received a new Message (" + data + ")";
            var icon = '';
            var title = Globalize.localize("TALYGEN") + ":" + Globalize.localize("UnifiedCommunication");
            var duration = 10000;
            if (!("Notification" in window)) {
                alert("This browser does not support desktop notification");
            }
            else if (Notification.permission === "granted") {
                displayNotificationChat(body, icon, title, duration, data);
            }
            else if (Notification.permission !== "denied") {
                Notification.requestPermission().then(function (permission) {
                    if (permission === "granted") {
                        displayNotificationChat(body, icon, title, duration, data);
                    }
                });
            }
