        truncateMessage(message) {
            const maxLength = 200;
            var gifRegex = /(https?:\/\/.*\.(?:gif))/i;

            // Regular expression to match emojis
            var emojiRegex = /[\u{1F600}-\u{1F64F}\u{1F300}-\u{1F5FF}\u{1F680}-\u{1F6FF}\u{2600}-\u{26FF}\u{2700}-\u{27BF}]/u;

            // Check if the text contains a GIF URL
            var hasGif = gifRegex.test(message);

            // Check if the text contains an emoji
            var hasEmoji = emojiRegex.test(message);

            // Check if the message contains an HTML img tag
            if (/<img[^>]+src="([^">]+)"/.test(message) || /[\u{1F600}-\u{1F64F}\u{1F300}-\u{1F5FF}\u{1F680}-\u{1F6FF}\u{2600}-\u{26FF}\u{2700}-\u{27BF}]/u.test(message)) {
                // If it contains an image tag, return the original message and Check if the text contains an emoji
                return message;
            }
            else {
                if (message.length > maxLength) {
                    // If it doesn't contain an image tag, truncate the message
                    return message.substring(0, maxLength) + '...';
                }
            }
            return message;
        },

        checkIsTyping: function (e) {

