            if (vm.txtMessage.trim() != '') {

                var toUser = vm.ChatUserListData.find(x => x.GROUP_ID == vm.userGroupId);
                if (toUser.IS_GROUP == true) {
                    // # Step 1: Split the string by underscore
                    var values = toUser.GROUP_NAME_ALIAS.split('_');
                    //   # Step 2: Remove duplicates
                    var unique_values = [...new Set(values)]
                    //   # Step 3: Join unique values with commas
                    var commaSaperatedGroupIds = unique_values.join(',');
                    vm.$socket.emit('is typing', { user: vm.sender.id, Group: vm.userGroupId, typing: true, IsGroup: true });
                }
                else {
                    if (toUser == undefined) {
                        toUser = vm.ChatUserListData.find(x => x.GROUP_ID == vm.userGroupId.toString());
                    }
                    if (e.which != 13 && vm.txtMessage != '') {
                        vm.$socket.emit('is typing', { user: toUser.RECEIVER_ID, Group: vm.userGroupId, typing: true, IsGroup: false });
                    }
                    else if (vm.txtMessage == '') {
                        vm.typing = false;
                        vm.$socket.emit('is typing', { user: toUser.RECEIVER_ID, Group: vm.userGroupId, typing: false, IsGroup: false });

                    }
                }
            }
