                "USER_ID": this.GetUserInfo.ID,
                "CONTACTNAME": this.GetUserInfo.Name
            })
            
            //this.ScrollBottomChat();
            // if(this.isFileActive==true)
            // {
            //     setTimeout(function () {
            //             this.ChatLinkClick();
            //     }, 1200);
            // }
        },
        async editGroup(itm) {
            await this.store.userByGroupId(`emaiId=${this.GetUserInfo.Email}&isGroup=1&room=${itm.grouP_ID}`)
            this.$parent.CreateGroupClick()
            this.topFilterSelect = '',
                this.updategroup = true
            this.Grouptitle = itm.grouP_NAME
            this.groupId = Number(itm.grouP_ID)
            this.selectedMembers = []
            this.store.getUserByGroupId.map((record) => {
                this.selectedMembers.push({
                    "USER_ID": Number(record.useR_ID),
                    "CONTACTNAME": record.useR_NAME
                })
            })
        },
        async deleteGroup(id) {
            var vm = this;
            vm.confirmR(vm.$t("ConfirmDeleteGroup"), true, (vm.$t("Delete") + "  " + +vm.$t("Records")), async function () {
                await vm.store.deleteGroup(`emaiId=${vm.GetUserInfo.Email}&groupId=${id}`)
                if (vm.store.getDeleteGroupRes) {
                    vm.ShowAlert(vm.$t("DeleteGroup"), "success", true, vm.$t("Alert"));
                    vm.store.groupList(`emaiId=${vm.GetUserInfo.Email}&isGroup=1&room=1`)
                } else {
                    vm.ShowAlert(vm.$t("Error"), "warning", true, vm.$t("Alert"));
                }
            })
        },
        groupBydates(messageList) {
            const groups = messageList.reduce((groups, data) => {
                const date = new Date(new Date(data.CREATED_AT).toDateString());
                if (!groups[date]) {
                    groups[date] = [];
                }
                groups[date].push(data);
                return groups;
            }, {});

            // Edit: to add it in the array format instead
            return Object.keys(groups).map((date) => {
                return {
                    date,
                    data: groups[date]
                };
            });
        },
        getTime(date) {
            date = new Date(date)
            const newInputDate = date;

            const timeStamp = new Date(newInputDate);
            return timeStamp.toLocaleTimeString(navigator.language, {
                hour: '2-digit',
                minute: '2-digit'
            })
        },
        deleteGroupUser(id, room) {
            var vm = this;
            let obj = {
                room: room,
                DeleteId: id,
                id: vm.GetUserInfo.ID,
                emaiId: vm.GetUserInfo.Email,
            }
            vm.confirmR(vm.$t("ConfirmDeleteGroupUser"), true, (vm.$t("Delete") + "  " + +vm.$t("Records")), async function () {
                DataService.DeleteUserFromGroup(obj).then((record) => {
                    vm.store.$state.userByGroupIdState = vm.store.getUserByGroupId.filter((item) => item.useR_ID !== id)

                    if (record.data) {
                        vm.ShowAlert(vm.$t("DeleteUserSuccess"), "success", true, vm.$t("Alert"));
                    } else {
                        vm.ShowAlert(vm.$t("Error"), "warning", true, vm.$t("Alert"));
                    }
                })

            })

        },
        /***********Group*********/

        /*********video call start ******/
        videoCreate(contect) {
            let obj = {
                USER_ID: contect.RECEIVER_ID,
                CONTACTNAME: contect.GROUP_NAME,
                USER_IMAGE: contect.AVATAR,
                GROUP_NAME_ALIAS: contect.GROUP_NAME_ALIAS,
                IS_GROUP: contect.IS_GROUP,
                room: contect.GROUP_ID

            }
            this.$parent.videoCreate(obj)
        },
        /*********video call End ******/
    },
    setup() {
        const store = communicationStore()
        const store2 = useloggedUserInfo()
        return {
            store,
            store2,
            getPreviousDay
        }
    }
}

</script>

