            display: function (data) {

                var vm = this;
                if (data.typing == true) {
                    if (data.IsGroup != true) {
                        if (data.Group == vm.userGroupId) {
                            var CurrnetChat = this.ChatUserListData.find(x => x.GROUP_ID == data.Group.toString());
                            vm.isTypingToGroup = CurrnetChat.GROUP_ID;
                            if (CurrnetChat != null && data.user == vm.sender.id) {
                                vm.isShowTypingForLeft = true;
                                setTimeout(function () {
                                    vm.isTypingToGroup = '';
                                    vm.isShowTypingForLeft = false;
                                }, 4000);

                            }
                            else {
                                vm.isShowTypingForLeft = false;
                            }

                        }
                        else {
                            var CurrnetChat = this.ChatUserListData.find(x => x.GROUP_ID == data.Group.toString());
                            vm.isTypingToGroup = CurrnetChat.GROUP_ID;
                            vm.isShowTypingForLeft = true;
                        }

                    }
                    else if (data.IsGroup == true) {

                        if (data.user != vm.sender.id) {
                            vm.isShowTypingForLeft = true;
                            var CurrnetChat = this.ChatUserListData.find(x => x.GROUP_ID == data.Group.toString());
                            vm.isTypingToGroup = CurrnetChat.GROUP_ID;
                            setTimeout(function () {

                                vm.isShowTypingForLeft = false;
                            }, 4000);
                        }
                    }

                }
                else {

