             */
            addPeer: function (data) {
                console.log("addPeer", data);
                var peer_id = data.peer_id;
                var rec_id = data.peer_id;
                if (peer_id in this.peers) {
                    console.log("Already connected to peer ", peer_id);
                    return;
                }
                this.peer_connection = this.createPeerConnection(peer_id);
                this.peers[peer_id] = this.peer_connection;
                this.setupIceCandidateHandler(this.peer_connection, peer_id);
                this.setupAddStreamHandler(this.peer_connection, rec_id, peer_id);
                this.peer_connection.addStream(this.localMediaStream);
                if (data.should_create_offer) {
                    this.createAndSendOffer(this.peer_connection, peer_id);
