             * Receiver open video call popup and start the call
             * @param {} data 
             */
            getNewVideo: function (data) {
               
                if (this.sender.id == data.id) {
                    console.log("getNewVideo", data);
                    let params_getNewVideo = this.sender;
                    const [token, alias, room] = data.room.split('~');
                    params_getNewVideo.room = room;
                    params_getNewVideo.alias = alias;
                    params_getNewVideo.token = token;
                    params_getNewVideo.receiver = data.receiver;
                    params_getNewVideo.isSender = 0;
                    params_getNewVideo.Receiver_name = this.replaceAll(data.contactname, "%20", " ");
                    this.incomingOutGoingParams = params_getNewVideo
                    this.VideoCallPopUP(params_getNewVideo);
                    setTimeout(() => {
                        this.openRoomChat(params_getNewVideo.room, this.sender, null, false);
                    }, 1000);
                }
            },
            /**
             * End to end streaming
             * @param {*} config 
             */
            sessionDescription: function (config) {
                const peer_id = config.peer_id;
                if (this.peers) {
                    console.log("checkk")
                    const peer = this.peers[peer_id];
                    const remote_description = config.session_description;
                    const desc = new RTCSessionDescription(remote_description);
                    if (peer) {
                        console.log("checkk22")
                        peer.setRemoteDescription(desc)
                            .then(() => {
                                if (remote_description.type === "offer") {
                                    return peer.createAnswer();
                                }
                            })
                            .then((local_description) => {
                                return peer.setLocalDescription(local_description);
                            })
                            .then(() => {
                                this.$socket.emit('relaySessionDescription', {
                                    peer_id: peer_id,
                                    session_description: peer.localDescription,
                                });
                            })
                            .catch((error) => {
                                console.log("Error setting remote description or creating answer: ", error);
                            });
                    }
                }

            },
            /**
             * generate ICE candidate for messaging
             * @param {*} data 
             */
            iceCandidate: function (data) {
                
                console.log("iceCandidate", data);
                var peer = this.peers[data.peer_id];
                var ice_candidate = data.ice_candidate;
                peer.addIceCandidate(new RTCIceCandidate(ice_candidate));
            },
            /**
             * Handle rejecting a video call
             * @param {*} param 
             */
            removePeer: function (config) {
                console.log("removePeer")
                // console.log('Signaling server said to remove peer:', config);
                var peer_id = config.peer_id;
                if (peer_id in this.peer_media_elements) {
                    peer_media_elements[peer_id].remove();
                    //talygen.alert();
                    this.ShowAlert(this.$t("VideoDisconnect"), "failure", true, this.$t("Alert"));
                }
                if (peer_id in this.peers) {
                    this.peer[peer_id].close();
                }

                delete this.peers[peer_id];
                delete this.peers[config.peer_id];
                if (this.localMediaStream != null) {
                    this.localMediaStream.getTracks().forEach(function (track) {
                        track.stop();
                    });
                    this.localMediaStream = null;
                    this.peers = {};
                    this.peers = {};
                }
                this.incomingOutGoingParams = ''
                this.videoCallShow = false
            },
            /**
             * Handle rejecting a video call
             * @param {*} param 
             */
            rejectNewVideo: function (param) {
                if (this.sender.id == Number(param.id) || this.sender.id == Number(param.receiver)) {
                    this.handleRejectNewVideo(param)
                }
            },
